import { LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAIL, LOGOUT_REQUEST, LOGOUT_SUCCESS, LOGOUT_FAIL, SET_SEARCH_MID } from "./actionTypes";

export const login = () => {
    return function (dispatch) {
        dispatch({
            type: LOGIN_REQUEST,
        });
        //eslint-disable-next-line no-undef
        VK.Auth.login((r) => {
            if (r.session) {
                const username = r.session.user.first_name;
                const mid = r.session.mid;
                dispatch({
                    type: LOGIN_SUCCESS,
                    payload: {
                        username: username,
                        mid: mid,
                    },
                });
            } else {
                dispatch({
                    type: LOGIN_FAIL,
                    error: true,
                    payload: new Error(" "),
                });
            }
        }, 4); //      photo
    };
};

export const logout = () => {
    return function (dispatch) {
        dispatch({
            type: LOGOUT_REQUEST,
        });
        //eslint-disable-next-line no-undef
        VK.Auth.logout((r) => {
            if (r) {
                dispatch({
                    type: LOGOUT_SUCCESS,
                });
            } else {
                dispatch({
                    type: LOGOUT_FAIL,
                    error: true,
                    payload: new Error("LogOut ERROR"),
                });
            }
        });
    };
};

export const setSearchMID = (search_mid) => {
    return function (dispatch) {
        dispatch({
            type: SET_SEARCH_MID,
            payload: search_mid,
        });
    };
};
